import styled from "styled-components";

import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import EmailIcon from '@mui/icons-material/Email';
import * as React from "react";
import Badge from "@mui/material/Badge";
import { mobile } from "../respn"
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { AfterLogin } from './AfterLogin';
import Kohsa from "../imgs/LOGO_PNG_2K.png"

export default function Navbar() {
  const Container = styled.div`
    
  
  `;

  const Wrapper = styled.div`
    padding: 10px 20px;
    display: flex;
    height:100%;
    width: 100%;
    justify-content: space-between;
    ${mobile({ padding: "10px 15px" })}
   
  `;

  const Left = styled.div`
    flex: 1;
    display: flex;
    align-items: center;
    ${mobile({ display: "none" })}
  `;

  const Right = styled.div`
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    ${mobile({ flex: 5, justifyContent: "flex-end" })}
  `;

  const Center = styled.div`
    flex: 1;
    align-items: center;
    margin:auto;
    
  `;

  const Language = styled.span`
    font-size: 20px;
    margin-right:10px;
    cursor: pointer;
    ${mobile({ fontSize: "15px", marginRight: "20px", display: "none" })}
   
    
  `;

  const SearchContainer = styled.div`
    
    display: flex;
    align-items: center;
    margin-left: 25px;
    padding: 5px;
    ${mobile({ display: "none" })}
  `;

  const Input = styled.input`
    border: none;
  `;

  const Logo = styled.img`
    width: 100px;
    height:70px;
  `;

  const MenuItem = styled.div`
    font-size: 18px;
    cursor: pointer;
    margin-left:25px;
    margin-right:25px;
    color :black;
    ${mobile({ fontSize: "15px", marginLeft: "10px", marginRight: "10px" })}
   

  `;
  const { currentuser } = useSelector((state) => state.user)

  const quantity = useSelector(state => state.cart.quantity)

  return (

    <Container>
      <Wrapper>
        <Left>
          <Language>EN</Language>

          <SearchContainer>

            <EmailIcon style={{ color: "grey", fontSize: "20px", marginRight: "5px" }} />
            <p> Kohsasupport@gmail.com</p>
          </SearchContainer>
        </Left>
        <Link to={"/"} style={{ textDecoration: 'none' }}>
          <Center>
            <Logo src={Kohsa} alt="logo" />
          </Center>
        </Link>
        <Right>
          {currentuser ? <AfterLogin /> : <><Link to={"/login"} style={{ textDecoration: 'none' }}>

            <MenuItem>Login</MenuItem>
          </Link>
            <Link to={"/register"} target="_blank" style={{ textDecoration: 'none' }}>
              <MenuItem>Signup</MenuItem>
            </Link></>}
          <Link to={"/cart"} style={{ textDecoration: 'none' }}>
            <MenuItem>
              <Badge badgeContent={quantity} color="primary">
                <ShoppingCartOutlinedIcon color="action" />
              </Badge>
            </MenuItem>
          </Link>
        </Right>
      </Wrapper>
    </Container>
  );
}
