
  import styled2 from "styled-components";
  import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
  import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
  import SearchOutlinedIcon from '@mui/icons-material/SearchOutlined';
  import { Link } from 'react-router-dom';
import { Typography,styled } from "@mui/material";
import { mobile } from './../respn';


  
  const Info = styled2.div`
    opacity: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 3;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.5s ease;
    cursor: pointer;
  `;
  
  const Container = styled2.div`
    flex: 1;
    margin: 5px;
    min-width: 300px;

    height: 450px;
    display: flex;

    justify-content: center;
    flex-direction: column;
    background-color: #f5fbfd;
    position: relative;
    &:hover ${Info}{
      opacity: 1;
    }
    &:active ${Info}{
      opacity: 1;
    }
    &:focus ${Info}{
      opacity: 1;
    }
${mobile({ width:"100%",height: "auto", margin: "5px 2.5px",border: "1px solid rgba(0, 0, 0, 0.1)"})}
  `;

  const Image = styled2.img`
    height: 75%;
    width: 100%;
    z-index: 2;
    ${mobile({ height: '100%',width: "100%"})}
    
  `;
  
  const Icon = styled2.div`
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
    transition: all 0.5s ease;
    &:hover {
      background-color: #e9f5f5;
      transform: scale(1.1);
    }
  `;

  const ItemTitle = styled(Typography)`
margin: 10px 10px 0 5px;
  font-family: inherit;
  font-weight:600;
  

  `
  const ItemDesc = styled(Typography)`
  margin: 0px 10px 0 5px;
    font-family: inherit;
    font-size:14px;
    font-weight:500;
    
  
    `
  const Price = styled(Typography)`
margin: 0 10px 0 5px;
  font-family: inherit;
  font-weight:600;
  color:#c400c1;

  `
  const Product = ({ item }) => {
    return (
      
      <Link style={{color:"inherit",textDecoration:"none"}} to={`/productpage/${item._id}`} >
      <Container>
       
        <Image src={item.img} />
        <Info>
          
          <Icon>
          <Link to={`/productpage/${item._id}`} >
            <SearchOutlinedIcon />
            </Link>
          </Icon>
        </Info>
        <ItemTitle variant="subtitle1" >{item.title}</ItemTitle>
        <ItemDesc variant="subtitle1" >{item.desc}</ItemDesc>
        <Price variant="subtitle1" >{item.price}00/-</Price>
      </Container>
      
      </Link>
    );
  };
  
  export default Product;