import React from 'react'
import { styled, Box, TextField, Typography, Divider } from '@mui/material';
import { mobile } from '../respn';
import { useDispatch ,useSelector, } from "react-redux";
import { useEffect } from 'react';
import { useState } from 'react';
import axios from "axios"

const Wrapper = styled(Box)`
margin: 0 25px;
background:white;
width: 100%;
font-family:inherit;
${mobile({margin: "25px 0px"})}

`

const InnerWrapper = styled(Box)`
padding:25px;
font-family:inherit;
`

const PersonalInfoTitle = styled(Typography)`
font-size:calc(1em + 1.25vw);
font-family:inherit;
font-family:inherit;
text-align:center;
`

const PersonalInfoSub = styled(Typography)`
text-align:center;
font-family:inherit;
font-family:inherit;

`
const TitleWrapper = styled(Box)`
margin-bottom:25px;
font-family:inherit;
`


const ImgWrapper = styled(Box)`
font-family:inherit;


`;

const InfoWrapper = styled(Box)`
padding:0 25px;
font-family:inherit;
width:170px;
${mobile({margin:"5px 0"})}
`;

const Info = styled(Typography)`
font-size:calc(0.5em + 0.5vw);
font-family:inherit;
${mobile({fontSize:"0.8rem"})}
`;


const OrderInfoSubWrapper = styled(Box)`
width:100%;
height:100%;
display:flex;
${mobile({flexDirection: 'column'})}
margin:15px 0;
align-items:center;
font-family:inherit;

`;

const Img = styled('img')({
  height:"150px",
  width:"150px",
  borderRadius:"10px",
  
})

export const OrderInfo = () => {
  const {currentuser} = useSelector((state) => state.user);
  const [orderitems, setorderitems] = useState([])
  console.log(currentuser._id)

  useEffect(()=>{
    const getProducts = async ()=>{
      try{
        const res = await axios.get(`https://kohsashop.herokuapp.com/api/order/find/${currentuser._id}`);
        setorderitems(res.data)
        console.log(res.data)
      }
      catch(err){

      }
    };
    getProducts()
  },[])
 

  
  return (
    <>


      <Wrapper>
      <InnerWrapper>
      <TitleWrapper><PersonalInfoTitle variant="h4">Your Orders</PersonalInfoTitle>
      <PersonalInfoSub variant="subtitle2">Hey there! Find your Orders here.</PersonalInfoSub></TitleWrapper>
      {orderitems.map((item)=>(
    
        <>
        {  item.products.map((product)=>(
          <>
        <OrderInfoSubWrapper>
     <ImgWrapper><Img src={product.img} alt="asda" /></ImgWrapper>
      <InfoWrapper>
        <Info style={{fontSize:"20px"}} fontWeight="bold" variant="subtitle1" >Product Info: <br /></Info>
        <Info variant="subtitle2" >{product.title}<br /></Info>
        <Info variant="subtitle2" >Size : {product.size}<br /></Info>
        <Info variant="subtitle2" >Price : {product.price}<br /></Info>
      </InfoWrapper>
      <InfoWrapper>
        <Info style={{fontSize:"20px"}} fontWeight="bold" variant="subtitle1" >Address: <br /></Info>
        <Info variant="subtitle2" >Area : Hubli<br /></Info>
        <Info variant="subtitle2" >City : Karnataka<br /></Info>
        <Info variant="subtitle2" >Country : India<br /></Info>
      </InfoWrapper>
      <InfoWrapper>
        <Info variant="subtitle1" >Quantity: 2 <br /></Info>
        <Info variant="subtitle1" >Price : 200/- <br /></Info>
      </InfoWrapper>
      <InfoWrapper>
        <Info variant="subtitle1" >Status: pending <br /></Info>
        <Info variant="subtitle1" >Track: N/A <br /></Info>
       
      </InfoWrapper>
     
  
  
      </OrderInfoSubWrapper>
      <Divider />
      </>
        ))}
     
      </>
      
      ))}
      
  
      </InnerWrapper>
      </Wrapper>
   

    </>
  )
}
