import React from 'react';
import { Link } from 'react-router-dom';
import styled from "styled-components";
import {mobile} from "../respn"



export default function CategoriesItems({item}) {
    const Container = styled.div`
flex:1;
margin:3px;
height:70vh;
position:relative;
`;

const Image = styled.img`
width: 100%;
height: 100%;
${mobile({height:"35vh",objectFit:"cover"})}
object-fit:cover;

`;

const Info = styled.div`
position:absolute;
width: 100%;
height: 100%;
top: 0;
left :0;
display:flex;
align-items:center;
justify-content:center;
flex-direction:column;
`;
const Title = styled.h1`
color:white;
margin-bottom: 20px;
font-weight:900;
`;
const Button = styled.button`
border:none;
padding:10px;
background-color:white;
font-color:grey;
cursor:pointer;
`;


  return (
    
<Container>
    <Link  to={"/products/limited"} >
    <Image src = {item.img}/>
    <Info>
        <Title>{item.title}</Title>
        <Button>SHOP NOW</Button>
    </Info>
    </Link>
    </Container>
  );
}
