
import React from 'react'
import { styled, Box, TextField, Typography, Divider, Button } from '@mui/material';
import { mobile } from '../respn';
import { useDispatch ,useSelector, } from "react-redux";
import { logout } from '../redux/userRedux';
import { Link } from 'react-router-dom';

const Wrapper = styled(Box)`
margin: 0 25px;
background:white;
width: 100%;
font-family:inherit;
${mobile({margin: "25px 0px"})}

`

const InnerWrapper = styled(Box)`
padding:25px;
font-family:inherit;
`

const PersonalInfoTitle = styled(Typography)`
font-size:calc(1em + 1.25vw);
font-family:inherit;
font-family:inherit;
text-align:center;
`

const PersonalInfoSub = styled(Typography)`
text-align:center;
font-family:inherit;
font-family:inherit;

`
const TitleWrapper = styled(Box)`
margin-bottom:25px;
font-family:inherit;
`



export const Settings = () => {
  const dispatch = useDispatch()
  return (
    <>


      <Wrapper>
      <InnerWrapper>
      <TitleWrapper><PersonalInfoTitle variant="h4">Settings</PersonalInfoTitle>
      <PersonalInfoSub variant="subtitle2">Nothing Much Here !</PersonalInfoSub></TitleWrapper>
      
      <Link style={{textDecoration:"none"}} to="/"><Button onClick={()=>dispatch(logout())} color="error" variant="contained">Logout</Button></Link>

      </InnerWrapper>
      </Wrapper>
   

    </>
  )
}
