import { Box,styled } from '@mui/material'
import React from 'react'
import { Typography } from '@mui/material';

const Wrapper = styled(Box)`
background:white;
padding:35px;
min-width: 300px;



`

const Subtitle = styled(Typography)`
margin: 8px 0;
color:#4a657a;
`
const NavItems = styled(Typography)`
margin-left: 25px;
color:#939393;
font-family: inherit;
margin-bottom:3px;
cursor:pointer;
&:hover{
    color:black;
}


`

const Title = styled(Typography)`
margin: 12px 0;

`




export const Navigation = ({setInformationpage,Informationpage}) => {
  return (
    <Wrapper>
        <Title variant='h6' fontWeight="bold" fontFamily="inherit" style={{textDecoration:"underline"}}>My Account</Title>
        <Box>
            <Subtitle variant='h6' fontWeight="bold" fontFamily="inherit" fontSize="19px;" >Profile</Subtitle>
            <NavItems variant='subtitle1' onClick={(e)=>setInformationpage("targetpersonal")}>Personal Information</NavItems>
            <NavItems onClick={(e)=>setInformationpage("targetorders")}>Orders</NavItems>
            <NavItems onClick={(e)=>setInformationpage("targetsettings")}>Settings</NavItems>
        </Box>
    </Wrapper>
  )
}
