import React from 'react'
import { styled, Box, TextField, Typography } from '@mui/material';
import { mobile } from '../respn';
import { useDispatch ,useSelector, } from "react-redux";

const Wrapper = styled(Box)`
margin: 0 25px;
background:white;
width: 100%;
${mobile({margin: "25px 0px"})}

`

const InnerWrapper = styled(Box)`
padding:25px;
`

const PersonalInfoTitle = styled(Typography)`
font-size:calc(1em + 1.25vw);
font-family:inherit;
text-align:center;
`

const PersonalInfoSub = styled(Typography)`
text-align:center;
font-family:inherit;

`
const TitleWrapper = styled(Box)`
margin-bottom:25px;
`

const InfoWrapper = styled(Box)`
display:flex;
width:100%;
${mobile({flexDirection: 'column',width:"100%",margin:"0",justifyContent:"center"})}
justify-content:space-evenly;
margin-bottom:25px;
`
const InfoWrapper2 = styled(Box)`
display:flex;
width:100%;
${mobile({flexDirection: 'column',width:"100%",margin:"0",justifyContent:"center"})}
justify-content:space-evenly;
`
const Infofield = styled(TextField)`

margin:0 50px;
font-size:calc(0.5em + 1vw);
${mobile({margin:"15px 0px"})}

`


export const PersonalInfo = () => {
  const {currentuser} = useSelector((state) => state.user);

  let date = Number(Date.parse(currentuser.createdAt));
  return (
    <Wrapper>
    <InnerWrapper>
    <TitleWrapper><PersonalInfoTitle variant="h4">Personal Information</PersonalInfoTitle>
    <PersonalInfoSub variant="subtitle2">Hey there! Find your details here.</PersonalInfoSub></TitleWrapper>
    <InfoWrapper> <Infofield id="outlined-basic" label="UserName" InputProps={{
            readOnly: true,
          }} defaultValue={currentuser.username}    variant="standard" />
    <Infofield id="outlined-basic" label="Email" InputProps={{
            readOnly: true,
          }} defaultValue={currentuser.email}    variant="standard" />
           
    </InfoWrapper>
    <InfoWrapper2> <Infofield id="outlined-basic" label="User ID" InputProps={{
            readOnly: true,
          }} defaultValue={currentuser._id}    variant="standard" />
           <Infofield id="outlined-basic" label="Account CreatedAt" InputProps={{
            readOnly: true,
          }} defaultValue={ new Date(date).toLocaleString()}   variant="standard" /></InfoWrapper2>
   

    </InnerWrapper>
    </Wrapper>
  )
}
