import React, { useState } from "react";
import { useLocation } from "react-router";
import styled from "styled-components";
import Announcement from "../components/Anounce";
import Footer from "../components/footer";
import Navbar from "../components/Navbar";
import Products from "../components/Products";
import Newsletter from "./../components/Newsletter";

import { mobile } from './../respn';

const Container = styled.div``;

const FilterContanier = styled.div`
  display: flex;
  justify-content: space-between;
  ${mobile({margin:"0 20px",display:"flex",flexDirection:"column",justifyContent:"center"})}
`;
const Filter = styled.div`
  margin: 20px;
  


`;

const Filtertext = styled.span`
  font-size: 20px;
  font-weight: 600;
  margin-right: 10px;

`;

const Select = styled.select`
padding: 5px;
margin-right: 20px;
font-size: 16px;`;
const Option = styled.option``;

export default function Productlist() {
  const location = useLocation()

  const cat = location.pathname.split('/')[2];
  const [sort,setSort] = useState('newest')


  

  return (
   
    <Container>
  
      <Announcement />
      <Navbar />

      <FilterContanier>
      
        <Filter>
          <Filtertext>Sort Products:   </Filtertext><br className="brfilter" />
          <Select onChange={(e)=>setSort(e.target.value)}>
            
           
            <Option value = "newest">Newest</Option>
            <Option value = "htl">Price (Higher to Lower)</Option>
            <Option value = "lth" >Price (Lower to Higher)</Option>
          </Select>
        </Filter>
      </FilterContanier>
      <Products  sort={sort} cat={cat} />
      <Newsletter />
      <Footer />
    </Container>
  );
}
