import axios from "axios"
import {loginStart,loginSuccess,loginFail} from "../redux/userRedux";


export const login = async (dispatch, user) =>{
    dispatch(loginStart());
    try{
        const res = await axios.post("https://kohsashop.herokuapp.com/api/auth/login",user)
        dispatch(loginSuccess(res.data))
        
    }   
    catch(err){  
        dispatch(loginFail());
    
    }
  
}

export const register = async (dispatch, user) =>{
    dispatch(loginStart());
    try{
        const res = await axios.post("https://kohsashop.herokuapp.com/api/auth/register",user)
        dispatch(loginSuccess(res.data))
        
    }   
    catch(err){  
        dispatch(loginFail());
    
    }
  
}
