import React from "react";
import styled from "styled-components";
import Announcement from "../components/Anounce";
import Footer from "../components/footer";
import Navbar from "../components/Navbar";
import { useState, useEffect } from "react"
import Newsletter from "../components/Newsletter";
import RemoveCircleOutlineIcon from '@mui/icons-material/RemoveCircleOutline';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';
import { mobile } from "../respn"
import { useLocation } from "react-router"
import { useDispatch } from "react-redux"
import axios from "axios"
import { addProduct, Total } from "../redux/cartRedux";
import { Box, Typography } from '@mui/material';
import LibraryBooksIcon from '@mui/icons-material/LibraryBooks';
import Alert from '@mui/material/Alert';
import AlertTitle from '@mui/material/AlertTitle';
import IconButton from '@mui/material/IconButton';
import Collapse from '@mui/material/Collapse';
import CloseIcon from '@mui/icons-material/Close';


const Container = styled.div``;

const Wrapper = styled.div`
  padding: 50px;
  display: flex;
  ${mobile({ flexDirection: "column", padding: "10px" })}
  
`;
const ImgCont = styled.div`
  flex: 1;
`;
const Image = styled.img`
  width: 100%;
  height: 90vh;
  object-fit: cover;
  ${mobile({ height: "40vh" })}
`;
const Title = styled.h1`
  font-weight: 400;
`;
const InfoCont = styled.div`
  flex: 1;
  padding: 0 50px;
  ${mobile({ padding: "10px" })}
`;
const Price = styled.span`
  font-weight: 200;
  color: black;
  font-size: 40px;
`;
const Desc = styled.p`
  margin: 20px 0;
`;

const FilterContanier = styled.div`
width: 50%;
margin: 30px 0;
display: flex;
justify-content: space-between;
${mobile({ width: "100%" })}
`;

const Filter = styled.div`
display: flex;
align-items: center;
`;

const FilterTitle = styled.span`
font-size: 20px;
font-weight:200;
color: black;
`;

const FilterColor = styled.p`
    width:20px;
    height: 20px;
    border-radius:50%;
    background-color: ${props => props.color};
    margin:0 5px;
    cursor: pointer;
`;


const FilterSize = styled.select`
margin-left:10px;
padding:5px;
`;



const FilterSizeOption = styled.option`

`;

const AddContanier = styled.div`
display:flex;
align-items: center;
width: 50%;
justify-content:space-between;
${mobile({ width: "100%" })}

`;

const Amountcont = styled.div`
display: flex;
align-items: center;
font-weight:700;

`;

const Amount = styled.span`
width:30px;
height:30px;
border-radius:10px;
border: 1px solid teal;
display: flex;
align-items: center;
justify-content:center;
margin:0 5px;
`;

const Button = styled.button`
padding:10px 15px;
border: 2px solid teal;
background-color:white;
cursor: pointer;
font-weight:500;
transition: all 0.3s ease-in-out;
&:hover{
  background-color:teal;
  color:white;
 
}
`;




export default function Prodcut() {
  const location = useLocation()

  const id = location.pathname.split('/')[2];

  const [product, setProduct] = useState({})
  const [quantity, setQuantity] = useState(1)
  const [color, setColor] = useState("")

  const [size, setSize] = useState("")

  const dispatch = useDispatch()

  useEffect(() => {
    const getProduct = async () => {
      try {
        const res = await axios.get(`https://kohsashop.herokuapp.com/api/product/find/${id}`);
        setProduct(res.data)
      }
      catch (err) {

        console.log(err);

      }
    };
    getProduct();
  }, [id])

  const handlequantity = (type) => {
    if (type === "des") {
      quantity > 1 && setQuantity(quantity - 1);
    }
    else {
      setQuantity(quantity + 1);
    }
  }

  const handleClick = () => {

    setOpen(true);
    dispatch(
      addProduct({ ...product, quantity, color, size })
    )
    dispatch(
      Total()
    )
    AutoClose();

  }

  const [open, setOpen] = useState(false);

  const AutoClose = () => {
    setTimeout(() => {
      setOpen(false);
    }, 3000);
  }

  return (
    <Container>
      <Announcement />
      <Navbar />
      <Wrapper>
        <ImgCont>
          <Image src={product.img} />
        </ImgCont>
        <InfoCont>
          <Title>{product.title}</Title>
          <Desc>
            {product.desc}
          </Desc>
          <Price>{product.price}/-</Price>
          <FilterContanier>

            <Filter>
              <FilterTitle>Size:</FilterTitle>
              <FilterSize onChange={(e) => { setSize(e.target.value) }}>
                {product.size?.map((c) => (
                  <FilterSizeOption key={c}>{c}</FilterSizeOption>
                ))}
              </FilterSize>
            </Filter>
          </FilterContanier>
          <Box style={{}} sx={{ width: '100%' }}>
            <Collapse in={open}>
              <Alert
                action={
                  <IconButton
                    aria-label="close"
                    color="inherit"
                    size="small"
                    onClick={() => {
                      setOpen(false);
                    }}
                  >
                    <CloseIcon fontSize="inherit" />
                  </IconButton>
                }
                sx={{ mb: 2 }}
              >
                Product Added To Cart Successfully
              </Alert>
            </Collapse>
          </Box>
          <AddContanier>
            <Amountcont>
              <RemoveCircleOutlineIcon onClick={() => handlequantity("des")} />
              <Amount>{quantity}</Amount>
              <AddCircleOutlineIcon onClick={() => handlequantity("incs")} />
            </Amountcont>

            <Button onclick={() => { handleClick(); AutoClose() }} onClick={handleClick} >Add To Cart</Button>
          </AddContanier>
          <Box style={{ margin: "20px 5px" }}><Typography variant="h6" fontWeight="bold" style={{ fontFamily: "inherit", display: "flex", alignItems: "center" }}>Products Details <LibraryBooksIcon style={{ marginLeft: "5px" }} /></Typography>
            <Typography>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Doloremque sequi sunt quis? Nostrum placeat laudantium quasi fugiat ab. Quibusdam iusto assumenda temporibus aperiam totam accusantium nisi inventore libero distinctio nemo!
              Error, temporibus corrupti sint optio ex earum illo sunt, accusamus voluptatibus ullam nostrum. Iure, numquam! Velit doloribus, accusantium aspernatur quis, enim porro blanditiis exercitationem voluptatem earum quia assumenda quibusdam qui.</Typography></Box>
        </InfoCont>
      </Wrapper>
      <Newsletter />
      <Footer />
    </Container>
  );
}
