import React from "react";
import styled from "styled-components";
import { mobile } from "../respn"
import { register } from './../redux/apicalls';
import { useDispatch, useSelector } from 'react-redux';
import { useState } from "react";

const Contanier = styled.div`
 width: 100vw;
 height: 100vh;
 background: linear-gradient(
      rgba(255, 255, 255, 0.3),
      rgba(255, 255, 255, 0.3)
    ),
    url("https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940")
      center;
      display:flex;
      background-size:cover;
      justify-content: center;
      align-items: center;
     

    
      `;
const Wrapper = styled.div`
padding:20px;
width:40%;
background-color:white;
box-shadow: 10px 20px 100px 10px grey;
border-radius:15px;
 ${mobile({ width: "90%" })}
`;
const Title = styled.h1`
font-size:24px;
font-weight:600;

`;
const Form = styled.form`
flex-wrap: wrap;
display:flex;

`;
const Input = styled.input`
flex:1;
min-width:40%;
margin: 20px 10px 0 0;
padding:10px;`;
const Agreement = styled.span`
font-size:12px;
margin:20px 0;`;
const Button = styled.button`
width:40%;
border:none;

padding:15px 10px;
background-color:teal;
color:white;
`;
const Error = styled.span`
  color:red;
`;

export default function Register() {
  const [fname, setfname] = useState("")
  const [lname, setlname] = useState("")
  const [username, setUsername] = useState("")
  const [email, setemail] = useState("")
  const [password, setPassword] = useState("")
  const [cpassword, setcPassword] = useState("")
  const dispatch = useDispatch()
  const { isfetching, error } = useSelector((state) => state.user)


  const handleregister = (e) => {
    e.preventDefault();
    register(dispatch, { fname, lname, username, email, password, cpassword })
    console.log(fname, lname, username, email, password)
  }
  return (
    <Contanier>
      <Wrapper>
        <Title>Create An Account</Title>
        <Form>
          <Input placeholder="First Name" onChange={(e) => { setfname(e.target.value); }} />
          <Input placeholder="Last Name" onChange={(e) => { setlname(e.target.value); }} />
          <Input placeholder="Username" onChange={(e) => { setUsername(e.target.value); }} />
          <Input placeholder="Email" onChange={(e) => { setemail(e.target.value); }} />
          <Input type="password" placeholder="Password" onChange={(e) => { setPassword(e.target.value); }} />

          <Agreement>


            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Ullam cum
            quo possimus esse consequuntur maiores deserunt, sed inventore
            veniam vitae rerum distinctio nulla laborum dolores dolore enim
            molestiae <b>Privacy Policy</b>
          </Agreement>

          <Button onClick={handleregister} disabled={isfetching}>Create Account</Button>
          {error === true ? <Error>  Please Make sure to fill all the input boxes / User Already Exists</Error> : <></>}
        </Form>
      </Wrapper>
    </Contanier>
  );
}
