
import styled from "styled-components";
import { popularProducts } from "../data";
import Product from "./Product";
import { useState,useEffect } from 'react';
import axios from "axios"
import { mobile } from './../respn';

const Container = styled.div`
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
`;

const Title = styled.h2`
text-align: center;
font-weight: 900;

`;




const Products = ({props,cat,sort}) => {
  
  const [products,setProducts] = useState([]);
  


  useEffect(()=>{
    const getProducts = async ()=>{
      try{
        const res = await axios.get( cat ? `https://kohsashop.herokuapp.com/api/product?category=${cat}` : "https://kohsashop.herokuapp.com/api/product?category=allahhuakbar" );
        setProducts(res.data)
      }
      catch(err){

      }
    };
    getProducts()
  },[cat])

  useEffect(()=>{
    if (sort==="newest"){
      setProducts(prev=>
        [...prev].sort((a,b)=>a.createdAt - b.createdAt )
        )
    }
    else if (sort==="htl"){
      setProducts(prev=>
        [...prev].sort((a,b)=>a.price - b.price )
        )
    }
    else{
      setProducts(prev=>
        [...prev].sort((a,b)=>b.price - a.price )
        )
    }
    
  },[sort])


  return (
    <>
    {cat? <Title>{cat} Products</Title>:<Title>All Products</Title>}
    <Container>
    
      {products.map((item) => (
        <Product item={item} key={item._id} />
      ))}
    </Container>
    </>
    
  );
  

};



export default Products; 