import React from "react";
import { Link } from 'react-router-dom';
export default function offerjs() {
  return (
    <>
     <Link to={"/products/Offer"} style={{ textDecoration: 'none' }}>
      <div className="row offer">
        <div className="col-lg-6 col-md-6 first">
          <p>
            25% OFF EVERYTHING!*
            <br />
            TIME TO CHECKOUT(-OUT)
            <br />
            With code: STYLES
          </p>
        </div>
        <div className="col-lg-6 col-md-6 second">
          <p>
            Free WorldWide Delivery <br /> on orders Above 899/-
          </p>
        </div>
        <div className="col-lg-12 third">
          <p>
            25% OFF EVERYTHING!
            <br />
            TIME TO CHECKOUT(-OUT)
            <br />
            <span className="subt"> With code: STYLES</span>
            <br />
            <span className="subt-t">
              See website banner for Ts&amp;Cs. Selected marked products
              excluded from promo.
            </span>
            <br />
          </p>
        </div>
      </div>
      </Link>
    </>
  );
}
