import { Box, styled } from '@mui/material'
import React from 'react'
import Footer from '../components/footer';
import { Information } from '../components/Information'
import { Navigation } from '../components/Navigation'
import Newsletter from '../components/Newsletter';
import Announcement from './../components/Anounce';
import Navbar from './../components/Navbar';
import { useState, useEffect } from 'react';
import { mobile,Tablet } from './../respn';


const Wrapper = styled(Box)`

display:flex;
margin:0 auto;
max-width:90vw;
justify-content:center;
margin-top:25px;
${Tablet({flexDirection: 'column'})}
`

export const ProfilePage = () => {

    const[Informationpage , setInformationpage] = useState("targetpersonal")

    useEffect(() => {
        console.log(Informationpage)
    },[Informationpage])

  return (
    <Box style={{backgroundColor:"#f7f7f7"}}>
    <Announcement/>
    <Navbar/>
   <Wrapper>
    <Navigation Informationpage={Informationpage} setInformationpage = {setInformationpage}/>
    <Information Informationpage={Informationpage}/>
   </Wrapper>
   <Newsletter/>
   <Footer/>
   </Box>
  )
}
