import { Box, Typography, styled } from "@mui/material";
import React from "react";
import Button from "@mui/material/Button";
import PersonIcon from "@mui/icons-material/Person";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import { Link } from 'react-router-dom';
import { useState } from "react";
import { borderBottom } from "@mui/system";
import { logout } from "../redux/userRedux";
import { useDispatch } from 'react-redux';


const MenuI = styled(MenuItem)`
  font-size: 14px;
  padding: 15px 60px 5px 24px;
  color: #4a4a4a;
`;

export const AfterLogin = () => {
  const dispatch = useDispatch()
  const Wrapper = styled(Box)`
    display: flex;
    align-items: center;
    margin-right: 20px;
    width:30px;
    height:30px;
    position:relative;
  `;

  const handleClose = () => {
    setOpen(false);
  };

  const handleClick = (e) => {
    setOpen(e.currentTarget);
  };

  const [open, setOpen] = useState(false);
  return (
    <>
   

      
     
    
    
      <PersonIcon  onClick={handleClick} style={{color:"black",fontSize:"30px"}}/>
      
    <Menu 
      id="basic-menu"
      anchorEl={open}
      open={open}
      onClose={handleClose}
      getContentAnchorE1 = {null}
      anchorOrigin={{
          vertical: 'top',
          horizontal: 'right',
        }}zz
        transformOrigin={{
          vertical: 'top',
          horizontal: 'bottom',
        }}
      MenuListProps={{
        "aria-labelledby": "basic-button"
      }}
    >
    
    <Link style={{textDecoration: 'none',color:"inherit"}} to="/profile"><MenuI  onClick={handleClose}>My Profile</MenuI></Link> 
     
      <Link style={{textDecoration: 'none',color:"inherit"}} to="/"><MenuI onClick={()=>dispatch(logout())}>Logout</MenuI></Link>
    </Menu>
   
  </>
  );
};
