import AddCircleOutline from "@mui/icons-material/AddCircleOutline";
import RemoveCircleOutline from "@mui/icons-material/RemoveCircleOutline";
import React, { useState } from "react";
import styled from "styled-components";
import Announcement from "../components/Anounce";
import Footer from "../components/footer";
import Navbar from "../components/Navbar";
import { mobile } from "../respn";
import { useDispatch, useSelector, } from "react-redux";
import { Decrease, RemoveAll, Increase, Total } from "../redux/cartRedux";
import { Link } from 'react-router-dom';




const Container = styled.div``;
const Wrapper = styled.div`
  padding: 20px;
  ${mobile({ padding: "10px" })}
`;
const Title = styled.h1`
  font-weight: 500;
  text-align: center;
`;
const Top = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  
`;

const Bottom = styled.div`
  display: flex;
  justify-content: space-between;
  ${mobile({ flexDirection: "column" })}
`;

const Info = styled.div`
  flex: 3;
`;

const Summary = styled.div`
  flex: 1;
  border: 0.5px solid lightgray;
  border-radius: 10px;
  padding: 20px;
  height: 55vh;
`;

const TopTexts = styled.div`
  ${mobile({ display: "none" })}
`;

const TopText = styled.span`
  text-decoration: underline;
  cursor: pointer;
  margin: 0 10px;
`;

const TopButton = styled.button`
  padding: 10px;
  font-weight: 600;
  cursor: pointer;
  border: ${(props) => props.type === "filled" && "none"};
  background-color: ${(props) =>
    props.type === "filled" ? "black" : "transparent"};
  color: ${(props) => props.type === "filled" && "white"};
`;

const Product = styled.div`
  display: flex;
  justify-content: space-between;
  ${mobile({ flexDirection: "column" })}
`;
const ProductDetails = styled.div`
  display: flex;
  flex: 2;
  ${mobile({ flexDirection: "column" })}
`;
const Image = styled.img`
  width: 250px;
  height: 270px;
  ${mobile({ width: "263px", height: "270px", margin: "auto" })}
`;

const Details = styled.div`
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
`;
const ProductName = styled.span``;
const ProductColor = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: ${(props) => props.color};
`;
const ProductID = styled.span``;
const ProductSize = styled.span``;
const PriceDetails = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
`;

const ProducutAmountContanier = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 10px;
`;
const ProductAmount = styled.div`
  font-size: 24px;
  margin: 5px;
`;
const ProductPrice = styled.div`
  font-size: 30px;
  font-weight: 300;
`;

const Hr = styled.hr`
  height: 1px;
  background-color: #5f5e5e;
  border: none;
`;

const SummaryItem = styled.div`
  margin: 30px 0;
  display: flex;
  justify-content: space-between;
  font-weight: ${(props) => props.type === "total" && "800"};
  font-size: ${(props) => props.type === "total" && "24px"};
`;
const Sumitemtext = styled.span``;
const SumitemPrice = styled.span``;
const Button = styled.button`
  width: 100%;
  background-color: black;
  color: white;
  font-weight: 600px;
  padding: 10px;
  border: none;
`;

const SumTitle = styled.h1`
  font-weight: 400;
`;

export default function Cart() {

  const dispatch = useDispatch()

  const HandleRemove = () => {
    dispatch(RemoveAll())
  }
  const cart = useSelector((state) => state.cart);


  const HandleDesc = (data) => {
    dispatch(Decrease({ data }))
    dispatch(Total())
  }

  const HandleInc = (data) => {
    dispatch(Increase({ data }))
    dispatch(Total())

  }

  const displayRazorPay = () => {
    const options = {
      "key": 'rzp_test_IgdOuYwxlTYXPj', // Enter the Key ID generated from the Dashboard
      "amount": cart?.total * 100, // Amount is in currency subunits. Default currency is INR. Hence, 50000 refers to 50000 paise
      "currency": "INR",
      "name": "Kohsa",
      "description": "Test Transaction",
      "image": "", //This is a sample Order ID. Pass the `id` obtained in the response of Step 1
      "handler": function (response) {
        alert(response.razorpay_payment_id);
        alert(response.razorpay_order_id);
        alert(response.razorpay_signature)
      },
      "prefill": {
        "name": "Gaurav Kumar",
        "email": "gaurav.kumar@example.com",
        "contact": "9999999999"
      },
      "notes": {
        "address": "Razorpay Corporate Office"
      },
      "theme": {
        "color": "#3399cc"
      }
    };
    var rzp1 = new window.Razorpay(options);
    rzp1.open();
    rzp1.on('payment.failed', function (response) {
      alert(response.error.code);
      alert(response.error.description);
      alert(response.error.source);
      alert(response.error.step);
      alert(response.error.reason);
      alert(response.error.metadata.order_id);
      alert(response.error.metadata.payment_id);
    });
  }


  const showRazorPay = () => {
    const script = document.createElement('script');
    script.src = "https://checkout.razorpay.com/v1/checkout.js"
    document.body.appendChild(script);
    script.onload = () => {
      displayRazorPay()

    }
  }



  return (
    <Container>
      <Announcement />
      <Navbar />
      <Wrapper>
        <Title>Your Bag ({cart.quantity})</Title>
        <Top>
          <Link to="/"><TopButton type="">Continue Shopping</TopButton></Link>
          <TopTexts>



          </TopTexts>
          <TopButton onClick={HandleRemove} type="filled">Remove All</TopButton>
        </Top>
        <Bottom>
          <Info>
            {cart.products.map(product => (


              <>
                <Product key={product._id}>




                  <ProductDetails>
                    <Image src={product?.img} />
                    <Details>
                      <ProductName>

                        <b>Product:</b>{product?.title}
                      </ProductName>
                      <ProductID>

                        <b>ID:</b> {product?._id}
                      </ProductID>
                      <ProductColor color={product?.color} />
                      <ProductSize>

                        <b>Size:</b> {product?.size}
                      </ProductSize>
                    </Details>
                  </ProductDetails>
                  <PriceDetails>
                    <ProducutAmountContanier>
                      <AddCircleOutline onClick={(e) => { HandleInc(product._id) }} />
                      <ProductAmount>{product?.quantity}</ProductAmount>
                      <RemoveCircleOutline onClick={(e) => { HandleDesc(product._id) }} />
                    </ProducutAmountContanier>
                    <ProductPrice>{product?.price * product?.quantity}/-</ProductPrice>
                  </PriceDetails>
                </Product>
                <Hr />
              </>
            ))}
          </Info>
          <Summary>
            <SumTitle>ORDER SUMMARY</SumTitle>
            <SummaryItem>
              <Sumitemtext>Subtotal</Sumitemtext>
              <SumitemPrice>{cart?.total}/- </SumitemPrice>
            </SummaryItem>
            <SummaryItem>
              <Sumitemtext>Estimated Shipping</Sumitemtext>
              <SumitemPrice> 199/-</SumitemPrice>
            </SummaryItem>
            <SummaryItem>
              <Sumitemtext>Shipping Discount</Sumitemtext>
              <SumitemPrice> -199/-</SumitemPrice>
            </SummaryItem>
            <SummaryItem type="total">
              <Sumitemtext>Total</Sumitemtext>
              <SumitemPrice> {cart?.total}/-</SumitemPrice>
            </SummaryItem>
            <Button onClick={showRazorPay}>BUY NOW</Button>
          </Summary>
        </Bottom>
      </Wrapper>
      <Footer />
    </Container>
  );
}
