import React from 'react'
import { PersonalInfo } from './PersonalInfo'
import { OrderInfo } from './OrderInfo';
import { Settings } from './Settings';
import { useEffect } from 'react';

export const Information = ({Informationpage}) => {

    // useEffect(()=>{

    // },[Informationpage])
  return (
    <>
        {(Informationpage === "targetpersonal" && <PersonalInfo/>) ||( Informationpage === "targetorders" && <OrderInfo/>) || ( Informationpage === "targetsettings" && <Settings/>) }
    </>
  )
}
