import React from "react";
import styled from "styled-components";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import TwitterIcon from "@mui/icons-material/Twitter";
import WhatsAppIcon from "@mui/icons-material/WhatsApp";
import MapIcon from '@mui/icons-material/Map';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import EmailIcon from '@mui/icons-material/Email';
import {mobile} from "../respn"
const Container = styled.div`
  display: flex;
  ${mobile({flexDirection:"column"})}
`;
const Left = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
`;

const Logo = styled.h1`
  font-weight: 900;
`;

const Description = styled.p`
  margin: 20px 0;
`;

const Social = styled.div`
  display: flex;
`;

const SocialIcon = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: white;
  background-color: #${(props) => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 20px;
`;

const Right = styled.div`
  flex: 1;
  padding: 20px;
  ${mobile({backgroundColor:"#fff8f8"})}
`;
const Center = styled.div`
  flex: 1;
  padding: 20px;
  ${mobile({display:"none"})}
  
`;

const Title = styled.h3`
margin-bottom:30px;
font-size:22.5px;
font-weight: 900;


`;

const List = styled.ul`
margin:0;
padding:0;
list-style-type: none;
display:flex;
flex-wrap: wrap;
`;

const Listitem = styled.li`
width:50%;
margin-bottom:10px;
`;


const Contactitem = styled.div`
margin-bottom:20px;
display:flex;
align-items: center;



`

const Payment = styled.img`
width:50%;

`

export default function Footer() {
  return (
    <>
      <Container>
        <Left>
          <Logo>Kohsa</Logo>
          <Description>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Placeat
            nemo ducimus ex eaque dicta, quidem modi nihil at voluptatum
            delectus reprehenderit nostrum quas molestiae commodi saepe
            consequuntur. Temporibus, perspiciatis nesciunt?
          </Description>
          <Social>
            <SocialIcon color="3B5999">
              <FacebookIcon />
            </SocialIcon>
            <SocialIcon color="E4405F">
              <InstagramIcon />
            </SocialIcon>
            <SocialIcon color="55ACEE">
              <TwitterIcon />
            </SocialIcon>
            <SocialIcon color="E60023">
              <WhatsAppIcon />
            </SocialIcon>
          </Social>
        </Left>
        <Center> <Title>Useful Links</Title>
        <List>
            <Listitem>Home</Listitem>
            <Listitem>Cart</Listitem>
            <Listitem>My Account</Listitem>
            <Listitem>Order Tracking</Listitem>
            <Listitem>WishList</Listitem>
            <Listitem>Orders</Listitem>
            <Listitem>Men Fashion</Listitem>
            <Listitem>Accessories</Listitem>
            <Listitem>Trending</Listitem>
            <Listitem>Support</Listitem>
        
        </List></Center>
        <Right>
            <Title>Contact</Title>
            <Contactitem> <MapIcon style={{marginRight:"10px"}} /> Your Address</Contactitem>
            <Contactitem> <LocalPhoneIcon style={{marginRight:"10px"}} /> +91 9538513924</Contactitem>
            <Contactitem> <EmailIcon style={{marginRight:"10px"}} /> youremail@gmail.com</Contactitem>
            <Payment src="https://i.ibb.co/Qfvn4z6/payment.png" />
        </Right>
      </Container>
    </>
  );
}
